.grid {
  display: grid;
  grid-template-rows: auto 1fr auto;
}

@media (max-width: 768px) {
  .grid {
    grid-template-columns: 1fr;
  }
}

.grid .footer {
  grid-column: 1/-1;
}

.grid .side-nav {
  grid-column: 1/2;
}

@media (max-width: 768px) {
  .grid .side-nav {
    grid-column: 1/-1;
  }
}

.grid .body {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 1rem;
  width: 100%;
  max-width: 93.75rem;
}

@media (max-width: 768px) {
  .grid .body {
    grid-column: 1/-1;
  }
}

.grid .body main {
  grid-column: 1/10;
}

@media (max-width: 768px) {
  .grid .body main {
    grid-column: 1/-1;
  }
}

.admin-page .grid .body main {
  grid-column: 1/-1;
}

.grid .body .layout-sidebar-first {
  grid-column: 10/13;
}

.grid.grid-full .body {
  max-width: 100%;
}

.grid.grid-full main {
  grid-column: 1/-1;
}

.full {
  grid-column: 1/-1;
}

body::before {
  color: red;
  position: fixed;
  left: 2rem;
  bottom: 2rem;
  z-index: 9999;
}

@media (min-width: 0) {
  body::before {
    content: "Extra Small";
